import styled from 'styled-components';
import { FunctionComponent } from 'react';
import WithoutTextSelection from '@2048/components/core/hoc/WithoutTextSelection';
import Center from '@2048/layout/Center';

export interface TileProps {
  color?: 'dark';
}

const DefaultTile = styled.div`
  height: 100%;
  width: 100%;
  text-align: center;
`;

const DarkTile = styled(DefaultTile)`
  background: black;
`;

export const Tile: FunctionComponent<TileProps> = ({ color, children }) => {
  let tile = DefaultTile;
  switch (color) {
    case 'dark':
      tile = DarkTile;
      break;
  }
  const StyledTile = WithoutTextSelection(tile);
  return (
    <StyledTile>
      <Center>{children}</Center>
    </StyledTile>
  );
};
